import { defineComponent, onMounted, VNode, Transition, computed } from 'vue';
import { RouterView } from 'vue-router';

import { useNaiveFBWrapper } from './composite/useNaiveFeedback';
import { useNaiveConfigProvider } from './composite/useNaiveTheme';
import useSettings from './composite/useSettings';
import useTRouterStore from './stores/tRouter';
import useSettingsStore from './stores/settings';

import UpdatePWA from './components/updatePWA/UpdatePWA';

import { initSettings } from './pages/settings/utils';

import './app.less';

const isNative = import.meta.env.VITE_PLATFORM === 'native';

export default defineComponent({
  name: 'App',
  setup() {
    const settingsStore = useSettingsStore();
    const tRouterStore = useTRouterStore();
    const naiveConfigProvider = useNaiveConfigProvider();
    const naiveFBWrapper = useNaiveFBWrapper();
    useSettings();

    onMounted(() => {
      // TODO add loading
      // 
      initSettings().then(res => {
        settingsStore.batchUpdateSettingMap(res);
      });
      // 
      document.addEventListener('contextmenu', (e) => {
        e.preventDefault();
      });
    });

    const transName = computed(() => {
      if (isNative) {
        const isBack = tRouterStore.isBack;
        return isBack ? 'page-slide-back' : 'page-slide';
      }
      return 'page-fade';
    });

    return () => (<>
      {naiveConfigProvider(
        naiveFBWrapper(
          <>
            <RouterView>
              {{
                default: ({ Component }: {
                    Component: VNode
                  }) => {
                  return (
                    <Transition name={transName.value} onAfterEnter={() => {
                      tRouterStore.updateIsBack(false);
                    }}>
                      {Component}
                    </Transition>
                  );
                }
              }}
            </RouterView>
            <UpdatePWA />
          </>
        )
      )}
    </>);
  }
});

