/**
 * @file settings
 * @author Yangholmes 2025-02-11
 */

import { SettingCatogory } from '@/pages/settings/utils';
import { db, Setting, SettingKey } from './db';
import { PickSome } from '@/typeUtils';

/**
 * 
 */
export function getAllSettings() {
  return db.settings.toArray();
}

/**
 * 
 * @param catogory 
 */
export function getSettingsByCategory(catogory: SettingCatogory) {
  return db.settings.where('catogory').equals(catogory).sortBy('index');
}

/**
 *  key 
 * @param key  key
 */
export function querySettingByKey(key: SettingKey) {
  return db.settings.get(key);
}

/**
 *  key 
 * @param key  key
 * @param value  value
 */
export function updateSettingValueByKey(key: SettingKey, value: Setting['value']) {
  return db.transaction('rw', [db.settings], () => {
    const now = new Date().getTime();
    return querySettingByKey(key).then(res => {
      if (!res) {
        return Promise.reject('Setting not found');
      }
      return res;
    }).then(oldSetting => {
      const newSetting = {
        ...oldSetting,
        value,
        updatedAt: now
      };
      return db.settings.put(newSetting);
    });
  });
}

/**
 * 
 * 
 * @param settings 
 */
export function batchUpdateSettingOptions(settings: PickSome<Setting, 'key' | 'label' | 'value' | 'index'>[]) {
  return db.transaction('rw', [db.settings], () => {
    const now = new Date().getTime();
    return getAllSettings().then(allSettings => {
      const newSettings = settings.map(newS => {
        const oldS = allSettings.find(s => s.key === newS.key);
        if (oldS === undefined) {
          // 
          const _setting: Setting = {
            readonly: false,
            hidden: false,
            catogory: 'general',
            control: { type: 'input' },
            ...newS,
            createDate: now,
            updateDate: now
          };
          return _setting;
        } else {
          // 
          const _setting: Setting = {
            ...oldS,
            ...newS,
            value: oldS.value,
            updateDate: now
          };
          return _setting;
        }
      });
      return db.settings.bulkPut(newSettings);
    });
  });
}

/**
 * 
 * @param key  key
 * @param value  value
 * @param label  label
 */
export function createSetting(
  setting: PickSome<Setting, 'key' | 'label' | 'value' | 'index'>,
) {
  return querySettingByKey(setting.key).then(res => {
    if (res) {
      return Promise.reject('Setting already exists, use updateSettingValueByKey instead');
    }
    return;
  }).then(() => {
    const now = new Date().getTime();
    const newSetting: Setting = {
      readonly: false,
      hidden: false,
      catogory: 'general',
      control: { type: 'input' },
      ...setting,
      createDate: now,
      updateDate: now
    };
    return db.settings.add(newSetting);
  });
}

/**
 * 
 * @param settings 
 */
export function batchCreateSettings(settings: PickSome<Setting, 'key' | 'label' | 'value' | 'index'>[]) {
  return db.transaction('rw', [db.settings], () => {
    const now = new Date().getTime();
    const newSettings = settings.map(setting => {
      const _setting: Setting = {
        readonly: false,
        hidden: false,
        catogory: 'general',
        control: { type: 'input' },
        ...setting,
        createDate: now,
        updateDate: now
      };
      return _setting;
    });
    return db.settings.bulkAdd(newSettings);
  });
}

export function clearAllSettings() {
  return db.settings.clear();
}
