import cn from 'classnames';
import { t } from '@/i18n';
import { router } from '@/router';

import { defaultSettings, settingCatogory } from './config';

import db from '@/services/db';
import { Setting } from '@/services/db/web/settings/db';

import SvgIcon from '@/components/svgIcon/SvgIcon';

import { NInput, NRadio, NRadioGroup, NSwitch } from 'naive-ui';

import styles from './controls.module.less';

/**  */
export type SettingControl = {
  description?: string;
  enabledBy?: string;
} & ({
  type: 'input';
} | {
  type: 'radio' | 'select';
  options: {
    iconName?: string;
    label: string;
    value: string | number | boolean;
  }[];
} | {
  type: 'switch';
  iconNames?: {
    on: string;
    off: string;
  }
} | {
  type: 'link';
  url: string;
} | {
  type: 'html';
  html: string;
} | {
  type: 'page';
  url: string;
})

/**  */
export type SettingCatogory = typeof settingCatogory[number]

/**
 * 
 * 
 */
export function initSettings() {
  const newVer = defaultSettings.find(s => s.key === 'version');
  return Promise.all([
    db.settings.querySettingByKey('initialized'),
    db.settings.querySettingByKey('version')
  ]).then(res => {
    const [sInit, sVer] = res;
    const settings = defaultSettings.map((s, index) => ({
      ...s,
      index
    }));
    // 
    if (!sInit) {
      return db.settings.batchCreateSettings(settings);
    } else if (newVer && sVer?.value !== newVer.value) {
      // 
      return db.settings.batchUpdateSettingOptions(settings).then(() => {
        // 
        return db.settings.updateSettingValueByKey('version', newVer.value);
      });
    }
  }).then(() => {
    return db.settings.getAllSettings();
  });
}

/**
 * 
 * @param setting 
 */
export function needOpenDecker(setting: Setting) {
  const isNeed = setting.readonly === false
    && setting.control?.type !== 'switch';
  return isNeed;
}

export function renderValue(setting: Setting, onChange?: (value: typeof setting.value) => void) {
  const { control } = setting;
  switch (control?.type) {
    case 'switch':
      return <NSwitch value={setting.value} onUpdateValue={(value) => onChange?.(value as boolean)} />;
    case 'radio': {
      const o = control.options.find(option => option.value === setting.value);
      return <div class={cn(styles['radio-value'])}>
        {o?.iconName ? <SvgIcon name={o.iconName} /> : null}
        {o?.label ? t(o.label) : '-'}
      </div>;
    }
    case 'link':
      return <span onClick={() => window.open(control.url)}>{setting.value}</span>;
    case 'page':
      return <span onClick={() => router.push(control.url)}>{setting.value}</span>;
    default:
      return setting.value;
  }
}

export function getControl(setting: Setting, onChange: (value: typeof setting.value) => void) {
  const { label, value, control } = { ...setting };
  switch (control?.type) {
    case 'radio':
      return <NRadioGroup
        class={cn(styles['radio-group'])}
        value={value}
        onUpdateValue={(value) => onChange(value)}
      >
        {control.options.map(option => {
          return <NRadio
            class={cn(styles.radio)}
            key={option.value.toString()}
            value={option.value}
          >
            <div class={cn(styles['radio-value'])}>
              {option.iconName ? <SvgIcon name={option.iconName} /> : null}
              {t(option.label)}
            </div>
          </NRadio>;
        })}
      </NRadioGroup>;
    case 'html':
      return <div innerHTML={control.html} />;
    case 'input':
      return <NInput
        placeholder={t('pages.settings.decker.control.input.placeholder', [label])}
        value={value.toString()}
        onInput={(value: string) => onChange(value)}
      />;
    default:
      return <>{value}</>;
  }
};
