import Dexie, { type EntityTable } from 'dexie';
import { BookInCellRawCover } from '@/pages/book/utils';

export type Cover = {
  id: string;
  /**  */
  file: Blob;
  /**  */
  hash: string;
  /**  */
  createDate: number;
  /**  */
  updateDate: number;
}

export const db = new Dexie('book') as Dexie & {
  book: EntityTable<BookInCellRawCover, 'id'>;
  covers: EntityTable<Cover, 'id'>;
};

db.version(1).stores({
  book: '&id, isbn, title, *authors, *subjects, *tags, cellId, bookcaseId, createDate, updateDate, [title+authors], [title+subjects], [title+tags]',
  covers: '&id, hash, createDate, updateDate'
});
