import * as nativeDb from './native';
import * as webDb from './web';

/**
 * 
 *  if else 
 */
function chooseDb() {
  if (import.meta.env.VITE_PLATFORM === 'native') {
    return nativeDb;
  }
  if (import.meta.env.VITE_PLATFORM === 'web') {
    return webDb;
  }
  return webDb;
}

//  top-level await 
export default chooseDb();
