import { JSX } from 'vue/jsx-runtime';

import useNaiveFeedbackStore from '@/stores/naiveFeedback';

import { NDialogProvider, NMessageProvider, useDialog, useMessage } from 'naive-ui';
import { MessageApiInjection, MessageReactive } from 'naive-ui/es/message/src/MessageProvider';

const messageTypes = ['create',
  'info',
  'success',
  'warning',
  'error',
  'loading',
] as const;

export type Placement = InstanceType<typeof NMessageProvider>['placement'];

type TMessage = {
  [key in typeof messageTypes[number]]: (placement: Placement, ...args: Parameters<MessageApiInjection[key]>) => MessageReactive;
}

export function useNaiveFeedback(placement: Placement = 'bottom') {
  const store = useNaiveFeedbackStore();

  const dialog = useDialog();
  const message = useMessage();

  const tMessage: TMessage = {} as TMessage;

  //  message
  messageTypes.forEach((type) => {
    tMessage[type] = (p: Placement, ...args: Parameters<MessageApiInjection[typeof type]>) => {
      const [content, options = {}] = args;
      const oldPlacement = store.placement;
      changePlacement(p);
      const result = message[type]?.(content, {
        ...options,
        onAfterLeave() {
          options.onAfterLeave?.();
          changePlacement(oldPlacement);
        }
      });
      return result;
    };
  });

  function changePlacement(newPlacement: Placement) {
    store.updatePlacement(newPlacement);
  }

  changePlacement(placement);

  return {
    dialog,
    message,
    /**  message  destroyAll  message.destroyAll() */
    tMessage,
    changePlacement
  };
}

export function useNaiveFBWrapper() {
  const store = useNaiveFeedbackStore();

  return (children: JSX.Element) => <NMessageProvider placement={store.placement}>
    <NDialogProvider>
      {children}
    </NDialogProvider>
  </NMessageProvider>;
}
