import { nextTick } from 'vue';
import { createI18n, I18nOptions } from 'vue-i18n';

import { supportLocalesOptions } from '@/pages/settings/config';

import en from './locales/en.json';

export const supportLocales = supportLocalesOptions.map(item => item.value);

export type SupportLocales = typeof supportLocales[number];

export type DefaultMessageType = typeof en;

/**
 * 
 * @param _locale 
 */
function initI18n(_locale = 'en') {
  let locale = _locale as SupportLocales;
  if (!supportLocales.some(l => l === locale)) {
    locale = 'en';
  }

  document.querySelector('html')?.setAttribute('lang', 'en');

  const i18n = createI18n<false, I18nOptions>({
    legacy: false,
    locale: 'en',
    fallbackLocale: 'en',
    messages: {
      en
    }
  });

  locale !== 'en' && changeLocale(locale);

  return i18n;
}

/**
 * 
 * @param _locale 
 */
function checkLocale(_locale: string) {
  let local = _locale;
  let isSupported = supportLocales.some(l => l === local);
  if(isSupported) {
    return local as SupportLocales;

  }
  const lo = local.split('-')[0];
  isSupported = supportLocales.some(l => l === lo);
  if(isSupported) {
    return lo as SupportLocales;
  }
  return null;
}

/**
 * 
 * @param _locale 
 */
export function changeLocale(_locale: string) {
  // _locale 
  let locale = checkLocale(_locale);
  if (!locale) {
    console.error(new Error(`Language ${_locale} not supported`));
    return ;
  }

  return import(`./locales/${locale}.json`).then(res => {
    const message = res.default as DefaultMessageType;
    i18n.global.setLocaleMessage(locale, message);
    i18n.global.locale.value = locale;
    document.querySelector('html')?.setAttribute('lang', locale);
    return nextTick();
  });
}

// export const i18n = initI18n('zh-CN');
export const i18n = initI18n(navigator.language);

export const t = i18n.global.t;
