/**
 * 
 * @param hashs  hash
 */
export function queryCoversByHash(hashs: string[]) {
  return db.transaction('r', [db.covers], () => {
    return db.covers.where('hash').anyOf(hashs).toArray()
      .then(res => res)
      .catch(err => {
        console.log(err);
        throw err;
      });
  });
}

/**
 * 
 * @param covers 
 */
function calcCoversHash(covers: Blob[]) {
  return Promise.all(covers.map((file) => {
    //  hash
    return calcBlobHash(file);
  }));
}

/**
 * 
 * 
 * 
 * @param covers 
 */
export function batchUpdateCovers(hashResults: BlobHashResult[]) {
  return db.transaction('rw', [db.covers], () => {
    // 
    const now = new Date().getTime();
    // 
    const allFiles = deduplicateList(hashResults, 'hash');
    //  hash 
    const hashs = allFiles.map((hash) => hash.hash);
    return queryCoversByHash(hashs).then(existingFiles => {
      // 
      const newFiles = allFiles
        .filter((hashResult) => !existingFiles
          .map(e => e.hash)
          .includes(hashResult.hash))
        .map(h => {
          return {
            id: uuidv4(),
            file: h.blob,
            hash: h.hash,
            createDate: now,
            updateDate: now,
          };
        });
      // 
      return db.covers.bulkAdd(newFiles).then(() => {
        // 
        return sortListByKey(existingFiles, newFiles, 'hash', hashs);
      });
    });
  });
}

/**
 * add a new book into cell.
 * first check covers hash, if not exist, add it,
 * then add the book.
 * @param book book object from scraper
 * @param cellId cell id
 * @param bookcaseId bookcase id
 */
export function addBook(book: Book, cellId: string, bookcaseId: string) {
  return calcCoversHash(book.covers).then(hashResults => {
    return db.transaction('rw', [db.book, db.covers], () => {
      // 
      const now = new Date().getTime();
      return batchUpdateCovers(hashResults).then((files) => {
        const ids = files.map((file) => file.id);
        // 
        const newBook = {
          ...book,
          covers: ids,
          cellId,
          bookcaseId,
          id: uuidv4(),
          createDate: now,
          updateDate: now,
        };
        // 
        return db.book.add(newBook).then(() => newBook);
      });
    });
  });
}

export function updateBook(book: BookInCell) {
  const { id, cellId, bookcaseId } = book;
  if (!id || !cellId || !bookcaseId) {
    throw new Error('idcellIdbookcaseId ');
  }
  return calcCoversHash(book.covers).then(hashResults => {
    return db.transaction('rw', [db.book, db.covers], () => {
      // 
      const now = new Date().getTime();
      return batchUpdateCovers(hashResults).then((files) => {
        const ids = files.map((file) => file.id);
        // 
        const newBook = {
          ...book,
          covers: ids,
          updateDate: now,
        };
        return db.book.update(id, newBook).then(() => newBook);
      });
    });
  });
}

export function deleteBook(id: string) {
  // TODO 
  return db.book.delete(id);
}

/**
 * 
 * @param srcCellsId  id 
 * @param dstCellId  id
 */
export function moveBooksToNewCell(srcCellsId: string[], dstCellId: string) {
  return db.transaction('rw', [db.book], () => {
    return db.book.where('cellId').anyOf(srcCellsId).modify((book) => {
      book.cellId = dstCellId;
    });
  });
}

export function clearAllBooks() {
  return db.transaction('rw', [db.book, db.covers], () => {
    return db.book.clear().then(() => db.covers.clear());
  });
}
