/**
 * @file bookcase web db baseon dexie.js
 *
 * index 
 * & 
 * ++ 
 * \* 
 * [A+B] 
 *
 * @see https://dexie.org/docs/API-Reference#schema-syntax
 * @author Yangholmes 2024-12-22
 */

import Dexie, { type EntityTable } from 'dexie';
import { Bookcase, Cell } from '@/pages/bookcase/utils';
import { Modify } from '@/typeUtils';

export const db = new Dexie('bookcase') as Dexie & {
  bookcases: EntityTable<
    Modify<Bookcase, { cells: string[]; }>,
    'id'
  >;
  cells: EntityTable<Cell, 'id'>;
};

db.version(1).stores({
  bookcases: '&id, name, createDate, updateDate',
  cells: '&id, name, bookcaseId, [bookcaseId+name], createDate, updateDate'
});
