import { ref } from 'vue';
import { defineStore } from 'pinia';
import { Setting, SettingKey } from '@/services/db/web/settings/db';

type SettingMap = Record<SettingKey, Setting['value']>

const useSettingsStore = defineStore('settings', () => {

  const settingMap = ref<SettingMap>({} as SettingMap);

  function batchUpdateSettingMap(allSettings: Setting[]) {
    const s = {} as SettingMap;
    allSettings.forEach((settings) => {
      s[settings.key] = settings.value;
    });
    settingMap.value = s;
  }

  function updateSettingMap(key: SettingKey, value: Setting['value']) {
    settingMap.value = {
      ...settingMap.value,
      [key]: value,
    };
  }

  return {
    settingMap, batchUpdateSettingMap, updateSettingMap,
  };
});

export default useSettingsStore;
