import { JSX } from 'vue/jsx-runtime';

import { GlobalThemeOverrides, NConfigProvider, NGlobalStyle } from 'naive-ui';

export function useNaiveConfigProvider() {
  const themeOverrides: GlobalThemeOverrides = {
    Button: {
      borderHover: 'none',
      borderFocus: 'none',
    },
    Input: {
      borderHover: 'none',
      boxShadowFocus: 'none'
    },
    Switch: {
      boxShadowFocus: 'none',
    },
    Radio: {
    }
  };

  return (children: JSX.Element) =>
    <NConfigProvider
      style={{ width: '100%', height: '100%' }}
      themeOverrides={themeOverrides}
    >
      <NGlobalStyle />
      {children}
    </NConfigProvider>;
}
