import { v4 as uuidv4 } from 'uuid';

import { db } from './db';

import { Bookcase, Cell } from '@/pages/bookcase/utils';

/**
 * get all boocases
 */
export function getBookcases() {
  return db.bookcases.orderBy('createDate').toArray();
}

/**
 * query bookcase by id
 * @param id bookcase id
 */
export function queryBookcaseById(id: string) {
  return db.bookcases.get(id).then(res => {
    if (!res) {
      return Promise.resolve(undefined);
    }
    const bookcase: Bookcase = { ...res, cells: [] };
    return queryCellsByBookcaseId(id).then((cells) => {
      bookcase.cells = cells;
      return bookcase;
    });
  });
}

export function queryCellInfo(cellId: string) {
  return db.cells.get(cellId);
}

export function queryCellsByBookcaseId(bookcaseId: string) {
  return db.cells.where('bookcaseId').equals(bookcaseId).toArray();
}

/**
 *  id 
 * 
 * @param cells 
 * @param bookcaseId  id
 */
function batchUpdateCells(cells: Cell[], bookcaseId: string) {
  const now = new Date().getTime();
  // 
  return queryCellsByBookcaseId(bookcaseId).then(eCells => {
    const cellsSet = new Set(cells.map(cell => cell.id));
    // 
    const dCellsId = eCells.filter(cell => !cellsSet.has(cell.id)).map(c => c.id);
    return db.cells.bulkDelete(dCellsId);
  }).then(() => {
    const _cells = cells.map(cell => ({
      id: uuidv4(),
      createDate: now,
      updateDate: now,
      ...cell,
      bookcaseId,
    }));
    // 
    return db.cells.bulkPut(_cells, undefined, { allKeys: true }).then(() => _cells);
  });
}

/**
 * 
 * @param bookcase 
 */
export function createNewBookcase(bookcase: Bookcase) {
  return db.transaction('rw', [db.bookcases, db.cells], () => {
    const now = new Date().getTime();
    const bookcaseId = uuidv4();
    // 
    return batchUpdateCells(bookcase.cells, bookcaseId).then(cells => {
      const _bookcase = {
        ...bookcase,
        cells: cells.map(cell => cell.id),
        id: bookcaseId,
        createDate: now,
      };
      // 
      return db.bookcases.add(_bookcase).then(() => {
        return { ..._bookcase, cells } as Bookcase;
      });
    });
  });
}

/**
 * 
 * @param bookcase 
 */
export function updateBookcase(bookcase: Bookcase) {
  return db.transaction('rw', [db.bookcases, db.cells], () => {
    if (!bookcase.id) {
      return Promise.reject('bookcase not exist');
    }
    const now = new Date().getTime();
    const bookcaseId = bookcase.id;
    return batchUpdateCells(bookcase.cells, bookcaseId).then((cells) => {
      const _bookcase = {
        ...bookcase,
        cells: cells.map(cell => cell.id),
        updateDate: now,
      };
      return db.bookcases.put(_bookcase).then(() => {
        return { ..._bookcase, cells } as Bookcase;
      });
    });
  });
}

/**
 * 
 * @param id bookcase id
 */
export function deleteBookcase(id: string) {
  return db.transaction('rw', [db.bookcases, db.cells], () => {
    return db.bookcases.delete(id).then(() => {
      return db.cells
        .where('bookcaseId')
        .equals(id)
        .primaryKeys().then((cellsId) => {
          return db.cells.bulkDelete(cellsId);
        });
    });
  });
}

export function clearAllBookcases() {
  return db.transaction('rw', [db.bookcases, db.cells], () => {
    return db.bookcases.clear().then(() => {
      return db.cells.clear();
    });
  });
}
