import { defineStore } from 'pinia';
import { ref } from 'vue';

const useTRouterStore = defineStore('tRouter', () => {
  // 
  const isBack = ref<boolean>(false);
  function updateIsBack(val: boolean) {
    isBack.value = val;
  }

  return {
    isBack,
    updateIsBack,
  };
});

export default useTRouterStore;