/**
 * @file SvgIcon
 * @author Yangholmes 2025-04-04
 */

import { defineComponent, ref, toRefs, watch } from 'vue';
import cn from 'classnames';
import styles from './svgIcon.module.less';

export default defineComponent({
  name: 'SvgIcon',
  props: {
    name: {
      type: String,
      required: true
    }
  },
  setup(props) {
    const { name } = toRefs(props);

    const getSvg = (iconName: string): Promise<string> => {
      return import(`./assets/${iconName}.svg`).then(res => {
        return res.default;
      });
    };

    const iconSrc = ref('');
    watch(name, (newName) => {
      getSvg(newName).then(res => {
        iconSrc.value = res;
      });
    }, {
      immediate: true
    });

    return () => (
      <div class={cn(styles['svg-icon'])} style={{
        backgroundImage: `url("${iconSrc.value}")`,
      }}>{''}</div>
    );
  }
});

