import { defineComponent, onMounted } from 'vue';
import { useI18n } from 'vue-i18n';

import { registerSW } from 'virtual:pwa-register';

import { useNaiveFeedback } from '@/composite/useNaiveFeedback';

export default defineComponent({
  name: 'UpdatePWA',
  setup() {
    const { t } = useI18n();
    const { dialog, message } = useNaiveFeedback();

    onMounted(() => {
      const updateSW = registerSW({
        onNeedRefresh() {
          dialog.info({
            title: t('components.updatePWA.refreshDialog.title'),
            content: t('components.updatePWA.refreshDialog.content'),
            positiveText: t('common.ok'),
            negativeText: t('common.nextTime'),
            closable: false,
            maskClosable: false,
            closeOnEsc: false,
            onPositiveClick() {
              updateSW();
            }
          });
        },
        onOfflineReady() {
          message.info(() => <>
            <div>{t('components.updatePWA.offlineReady.info.line1')}</div>
            <div>{t('components.updatePWA.offlineReady.info.line2')}</div>
          </>);
        },
      });
    });

    return () => null;
  }
});
