import { ref } from 'vue';
import { defineStore } from 'pinia';

import { Placement } from '@/composite/useNaiveFeedback';

const useNaiveFeedbackStore = defineStore('naiveFeedback', () => {
  const placement = ref<Placement>('top');
  function updatePlacement(newPlacement: Placement) {
    placement.value = newPlacement;
  }

  return {
    placement,
    updatePlacement,
  };
});

export default useNaiveFeedbackStore;